.item {
  width: 90%;
  height: 14rem;
  padding: 2rem;
  display: flex;
  flex-direction: row;
  background-color: white;
  border-radius: 12px;
  margin: 2rem auto;
  cursor: pointer;
}
.item img {
  width: 10rem;
  height: 10rem;
  border-radius: 12px;
}

.data_container {
  padding: 0.4rem 1rem;
  border-bottom: 1px solid rgba(0, 0, 0, 0.2);
}

.title {
  font-size: 1.8rem;
  font-weight: 700;
}

.rating {
  font-size: 1.6rem;
  font-weight: 500;
}

@media screen and (max-width: 420px) {
  .item {
    width: calc(100% - 1.5rem);
    padding: 1.4rem;
  }
}
