.header {
  width: 768px;
  /* background-color: rgb(215, 104, 89); */
  text-align: center;
  position: relative;
  padding: 1.6rem 0;
}
.img {
  width: 10rem;
}

/* .title {
  font-size: 3.2rem;
  font-weight: 900;
  cursor: pointer;
  /* color: white; }*/

.login_button {
  position: absolute;
  top: 1.6rem;
  right: 3rem;
  cursor: pointer;
  background-color: white;
  /* color: white; */
  border: none;
  outline: none;
  /* padding: 0.7rem 1.4rem; */
  /* border-radius: 16px; */
  font-size: 1.8rem;
}

@media screen and (max-width: 768px) {
  .header {
    width: 100%;
  }
}

@media screen and (max-width: 420px) {
  .header {
    text-align: initial;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    padding: 2rem 1.5rem;
  }
  /* .title {
    font-size: 2.8rem;
  } */
  .login_button {
    top: 2rem;
    right: 1.5rem;
  }
}

.barIcon {
  cursor: pointer;
  margin-bottom: 2px;
  position: absolute;
  top: 1.6rem;
  left: 3rem;
  cursor: pointer;
  background-color: white;
  /* color: white; */
  border: none;
  outline: none;
  /* padding: 0.7rem 1.4rem; */
  font-size: 1.8rem;
}
